@media only screen and (max-width: 1220px) {
  .section {
    .container {
      padding: 10px 0;
    }
  }

  .home {
        .main-title-wrapper {
            margin: -15% 0 0 0;

            h1 {
                margin: 14vw 0 4rem 0;
            }

            h2 {
                margin: 0 0 1rem 0;
            }
        }
    }

  .indications {
    .indications-wrapper {
      flex-direction: column;

      ul {
        margin: 0;
      }

      img {
        max-width: 100%;
        margin: 0 3vw;
      }
    }
  }

  .contact-data {
    flex-direction: column;
  }
}
